h1 {
  text-align: left;
  margin-left: 40px;
}
#search {
  float: left;
  border-top: none;
  border-left: none;
  border-right: none;
  margin: 30px;
  padding-top: 20px;
  padding-left: 10px;
  padding-right: 400px;
  background-color: white;
  padding-bottom: 5px;
  box-shadow: 3px 3px 1px aliceblue;
}
#reset {
  background-color: #20b883;
  border-radius: 35px;
  border: none;
  padding: 20px;
  margin-left: 0px;
  float: left;
  margin-top: 18px;
}
th {
  background-color: white;
}
td {
  /* margin: 1px; */
  padding-left: 0px;
  padding-right: 40px;
  padding-top: 10px;
  padding-bottom: 10px;
  background-color: white;
}
tr {
  background-color: white;
  row-gap: 2px;
  -moz-box-shadow: 0px 2px 2px black;
  box-shadow: 0px 2px 3px solid black;
}
table {
  margin-left: 30px;
  margin-top: 0px;
  border-collapse: separate;
  border-spacing: 0 15px;
  width: 100%;
  border-left: none;
}
